import localForage from "localforage";
import { createContext, useEffect, useState } from "react";
import * as Realm from "realm-web";
const APP_ID = "artcomp-github-cwvac";
export const MongoContext = createContext(null);
const mongoRealm = new Realm.App({ id: APP_ID });
const credentials = Realm.Credentials.anonymous();

export const MongoContextProvider = ({ children }) => {
  const [user, setUser] = useState();
  // const [imageMap, setImageMap] = useState(new Map());

  // const addToImageMap = (id, value) => {
  //   const copy = new Map(imageMap);
  //   copy.set(id, value);
  //   setImageMap(copy);
  //   console.log(copy);
  // };

  useEffect(() => {
    try {
      mongoRealm.logIn(credentials).then((u) => setUser(u));
    } catch (err) {
      console.error("Failed to log in", err);
    }
  }, []);

  const addToImageMap = (id, value) => {
    localForage
      .setItem(id, value)
      .then(function (value) {
        // console.log(value);
      })
      .catch(function (err) {
        console.log(err);
      });
  };

  const getImageData = (id) => {
    localForage
      .getItem(id)
      .then(function (value) {
        return value;
      })
      .catch(function (err) {
        console.log(err);
      });
  };

  return (
    <MongoContext.Provider
      value={{ id: APP_ID, user, getImageData, addToImageMap }}
    >
      {children}
    </MongoContext.Provider>
  );
};
